/* SCROLLBAR */

::-webkit-scrollbar {
  width: 2px;
}

::-webkit-scrollbar-track {
  background: #1f2937;
}

::-webkit-scrollbar-thumb {
  background: #043C2FFF;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #043C2FFF;
}

/* FIREFOX */

* {
  scrollbar-width: none;
  /* "auto", "thin", ou "none" */
  scrollbar-color: #043C2FFF #1f2937;
  /* Couleur du pouce et de l'arrière-plan */
}

/* MODALS */

/* Modal styles */

.ngneat-dialog-content {
  position: fixed !important;
  /* Position fixe pour centrer dans l'écran */
  top: 500;
  left: 50%;
  transform: translate(-50%, -50%);
  /* Centre horizontalement et verticalement */
  z-index: 1050;
  /* S'assure qu'il reste au-dessus des autres éléments */
  width: auto;
  /* Ajuste automatiquement la taille */
  max-width: 90%;
  /* Limite la largeur */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
  /* Ajoute une ombre */
  border-radius: 8px;
  /* Coins arrondis */
  background: #fff;
  /* Fond blanc pour le popup */
}

